@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*article meta*/
@mixin article-meta-text-size() {
	@include font(1.2rem, 1.4); }

@mixin article-meta-text-color() {
	color: $text-color-light;
	fill: $text-color-light; }

.article-meta {
	@include article-meta-text-size;
	@include article-meta-text-color;
	margin-bottom: $bottom-space-md;
	@include clear; }

/*article meta end*/

/*date*/
.datetime {
	float: left;
	display: block;
	margin-bottom: $bottom-space;
	@include article-meta-text-size;
	@include article-meta-text-color;
	font-style: normal;

	.article-meta & {
		margin-right: 20px;
		margin-bottom: 0; } }

/*date end*/

/*visit*/
.visit-view {
	//cursor: default

	svg {
		@include display-inline-block-middle;
		@include size(18px);
		margin-right: 10px;

		path {
			@include article-meta-text-color;
			transition-property: fill;
			transition-duration: $animation-speed; }

		&.depict-visit-share {
			@include size(12px); } }

	& > em {
		@include display-inline-block-middle;
		font-style: normal;
		transition-property: color;
		transition-duration: $animation-speed; }

	//.no-touchevents &:hover path
	//ill: $text-color

	//.no-touchevents &:hover > em
 }	//olor: $text-color

.visit {
	overflow: hidden;
	margin-left: 20px;
	text-align: right;
	@include article-meta-text-size;
	@include article-meta-text-color;
	//cursor: default
	display: none; }

.visit__item {
	display: inline-block;
	vertical-align: top;
	margin-left: 30px;

	@include media('<mob-lg') {
		margin-left: 5px; } }

.visit__inner {
	display: inline-block;
	text-decoration: none;
	@include article-meta-text-color; }

/*visit end*/
