@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*REGION HEADER*/
.region-header {
	position: relative;
	z-index: 5;
	//padding: round($grid-layout-space*0.85) #{$grid-layout-space - 9px} // -9 для компенсации вертикального скролла
	padding: round($grid-layout-space*0.5) #{$grid-layout-space - 9px}; // -9 для компенсации вертикального скролла
	min-height: 100px;

	@include media('<desk-hd') {
		//padding: round($grid-layout-space--desk-hd*0.85) #{$grid-layout-space--desk-hd - 9px} // -9 для компенсации вертикального скролла
		padding: round($grid-layout-space--desk-hd*0.5) #{$grid-layout-space--desk-hd - 9px}; } // -9 для компенсации вертикального скролла

	@include media('<desk-md') {
		//padding: round($grid-layout-space--desk-md*0.85) $grid-layout-space--desk-md
		padding: round($grid-layout-space--desk-md*0.5) $grid-layout-space--desk-md; }

	@include media('<tab-lg') {
		//padding: round($grid-layout-space--desk-md*0.85) $grid-layout-space--desk-md
		padding: round($grid-layout-space--desk-md*0.5) $grid-layout-space--desk-md; }

	@include media('<mob-lg') {
		//padding: round($grid-layout-space--mob-lg*0.85) $grid-layout-space--mob-lg
		padding: round($grid-layout-space--mob-lg*0.5) $grid-layout-space--mob-lg; }

	&::before {
		@include pseudo();
		@include cover($position: null);
		@include linear-gradient(to right, rgba(0, 0, 0, 0.75) 0, rgba(0, 0, 0, 0.50) 20%, rgba(0, 0, 0, 0) 100%);
		z-index: -1;

		@include media('<mob-lg') {
			background: rgba(0, 0, 0, 0.35); } } }

.region-header__bg {
	@include cover();
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	z-index: -2; }

.region-header__caption {
	margin-bottom: 0.5em;
	//min-height: calc(1em*1.2*2)
	font-family: $font-alt;
	font-weight: 700;
	@include font(36px, 1.2);
	color: white;
	text-shadow: 1px 1px 0 black;
	@include clear;

	@include media('<desk-md') {
		font-size: 32px; }

	@include media('<tab-lg') {
		min-height: calc(1em*1.3*2);
		@include font(28px, 1.3); }

	@include media('<mob-lg') {
		min-height: calc(1em*1.4*2);
		@include font(22px, 1.4); }

	.col {
		float: left;
		width: 49.98%;
		padding-right: $grid-space;
		text-decoration: none;
		color: inherit;

		@include media('<mob-lg') {
			width: 100%;
			padding: 0;
			margin-bottom: $grid-space;

			&:last-child {
				margin-bottom: 0; } }

		& > img {
			float: left;
			@include size(48px);
			margin-right: 10px;

			@include media('<tab-lg') {
				@include size(36px); }

			@include media('<mob-lg') {
				@include size(32px); } } }

	h1, h2, h3 {
		overflow: hidden;
		font-family: inherit;
		font-weight: inherit;
		@include font(inherit, inherit);
		color: inherit;
		margin: 0; }

	h3 {
		@include font(1.8rem, round-float(24/18, 2)); } }

.region-header__caption, .region-header__holder {
	max-width: 100%; }

.region-header__caption {
	width: 1000px; }

.region-header__holder {
	width: 850px; }

.region-header__holder {
	@include clear;

	.region-logo {
		float: left;
		width: 80px;
		margin-right: 20px;

		@include media('<mob-lg') {
			float: none;
			width: 70px;
			margin-right: 15px;
 } } }			//margin-bottom: $grid-inside-vertical-space

.region-header__info {
	overflow: hidden;
	color: white;
	@include clear-spacing;

	li {
		display: inline-block;
		vertical-align: top;
		padding-right: 20px;
		width: 50%;
		@include font(1.4rem, 1.5);
		letter-spacing: normal;
		//+margin-vertical(round-float($bottom-space*0.4, 1))
		@include margin-vertical(0.6em);
		@include clear;

		@include media('<mob-lg') {
			font-size: 1.6rem;
			width: 100%;
			padding-right: 0; }

		& > span {
			display: block;
			overflow: hidden; } }

	svg {
		float: left;
		width: 24px;
		height: 24px;
		margin-right: $grid-space; }

	path {
		fill: white; }

	a {
		color: inherit;
		text-decoration: none; } }

/*REGION HEADER end*/
