@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*news grid*/
.news-grid {
 }	//overflow: hidden

.news-grid--main {
	//max-width: 850px
	max-width: 1160px; }

.news-grid__list {
	margin-bottom: -$grid-space;
	@include margin-horizontal(round(-$grid-space/2));
	@include clear; }

.news-grid__item, .news-grid__item--large, .news-grid__item--wide {
	position: relative;
	@include font($font-size-small, $line-height-small);
	letter-spacing: normal;

	.news-grid & {
		float: left;
		margin-bottom: $grid-space;

		width: $news-item-width; // костыль для малекнького элемента, чтобы высоты блоков были четко по дизу
		height: $news-item-height; // костыль для малекнького элемента, чтобы высоты блоков были четко по дизу
		@include padding-horizontal(round($grid-space/2));

		@include media('<desk-lg') {
			width: 49.98%; }

		@include media('<mob-lg') {
			width: 100%;
			height: auto; } }

	.news-grid__slider & {
		margin-bottom: 0;
		@include padding-horizontal(0); }

	.news-simple & {
		height: auto;
		width: auto; } }

// специальные свойства для блока "Главные новости" (`.news-grid--main`)
// или специальные свойства элемета новостей с классом `.news-grid__item--no-img`
.news-grid--main .news-grid__item:nth-child(n + 4), .news-grid .news-grid__item--no-img { // Костыль. Все элементы начиная с четвертого должны быть без картинок и лейблов
	height: $news-item-sm-height; // костыль для элемента без картинок, чтобы высоты блоков были четко по дизу

	@include media('<mob-lg') {
		height: auto; }

	.news__label {
		display: none; }

	.news-grid__img {
		display: none; } }

.news-grid .news-grid__item--large {
	width: $news-item-lg-width; // костыль для большого элемента, чтобы высоты блоков были четко по дизу
	height: $news-item-lg-height; // костыль для большого элемента, чтобы высоты блоков были четко по дизу

	@include media('<desk-lg') {
		width: 100%; }

	//+media('<mob-lg')
	@include media('<desk-lg') {
		//width: 100%
		height: auto; } }

.news-grid .news-grid-slider__slide {
	@include media('<desk-lg') {
		height: 100%; } }

.news-grid__inner {
	display: block;
	position: relative;
	text-decoration: none;
	color: $text-color;
	background: #fff;
	box-shadow: inset 0 -3px 0 $mark-color-alt;
	height: 100%; // inner по высоте равен item
	overflow: hidden; // необходимо для учета margin внутренних элементов !!!

	.news-simple & {
		background: none;
		box-shadow: none; } }

.news-grid__item--has-img {

	.news-grid__inner {
		position: relative;

		&::before {
			@include pseudo();
			@include cover($position: null, $height: $news-item-img-height); // высота как у картинки. см. выше
			background: url(../../img/no-img.jpg) no-repeat center;
			background-size: cover; } } }

.news-grid__item--large {

	.news-grid__inner::before {
		height: $news-item-lg-img-height; } }
// высота как у большой картинки. см. выше

.news-grid__img {
	min-height: $news-label-size;
	background-image: url(../../img/no-img.jpg);
	background-repeat: no-repeat;
	background-position: center top;
	background-size: cover;
	z-index: 4;

	&[style*="preloader.svg"] {
		background-size: 48px;
		background-position: center;
		background-color: #f3f3f3; }

	&.position--left-top {
		background-position: left top; }

	&.position--center-top {
		background-position: center top; }

	&.position--right-top {
		background-position: right top; }

	&.position--left-center {
		background-position: left center; }

	&.position--center-center {
		background-position: center center; }

	&.position--right-center {
		background-position: right center; }

	&.position--left-bottom {
		background-position: left bottom; }

	&.position--center-bottom {
		background-position: center bottom; }

	&.position--right-bottom {
		background-position: right bottom; }

	img {
		width: 100%; }

	.news-grid__slider + .news-grid__inner & {
		display: none; } }

//.news-grid__slider,
.news-grid__img {
	position: relative;
	height: $news-item-img-height; // костыль, чтобы высоты блоков были четко по дизу
	z-index: 4;
	overflow: hidden;

	.news-grid__item--large & {
		height: $news-item-lg-img-height;

		@include media('<mob-lg') {
			height: $news-item-lg-img-height-mob-lg; } }

	.news-grid--main &, .news-grid__item--has-img & {
		position: absolute;
		top: 0; } }

.news-grid--main, .news-grid__item--has-img {
	.news-grid__img {
		left: 0;
		width: 100%; }

	.news-grid__slider {
		left: round($grid-space/2);
		right: round($grid-space/2);
		width: auto; } }

.news-grid-slider {
	.news-grid__img {
		background: #ddd;

		img {
			position: absolute;
			display: block;
			left: 50%;
			top: 50%;
			width: 48px;
			height: 48px;
			margin: -24px 0 0 -24px;

			&.loaded {
				margin: 0;
				width: 100%;
				height: 100%;
				left: 0;
				top: 0;
				max-width: none;
				@include object-fit(cover); } } } }

.news-grid__content {
	position: relative;
	overflow: hidden;
	padding: $grid-inside-vertical-space $grid-inside-horizontal-space 48px;
	// Костыль. Нужен больше нижний отступ, чтобы повесить на абсолюте мета инфу

	.news-grid & {
		// Костыль. Жесткая высота контента. Высота элемета - высота картинки
		height: $news-item-content-height;

		@include media('<mob-lg') {
			height: auto; } }

	.news-grid .news-grid__item--large & {
		// Костыль. Для больших блоков своя высота контента. Высота элемета - высота картинки
		height: $news-item-lg-content-height;

		//+media('<mob-lg')
 }		//eight: auto

	.news-grid--main .news-grid__item:nth-child(n+4) & {
		// Костыль. Для мленьких блоков своя высота контента. По дизу.
		height: $news-item-sm-content-height;

		@include media('<mob-lg') {
			height: auto; } }

	// специальные свойства для блока "Главные новости" (`.news-grid--main`)
	// или специальные свойства элемета новостей с классом `.news-grid__item--has-img`

	// 1) для первых трех элементов или с классом `.news-grid__item--has-img`
	.news-grid--main .news-grid__item:nth-child(-n+3) &, .news-grid .news-grid__item--has-img & {
		margin-top: $news-item-img-height; } // Костыль. Высота картинки

	// 2) то же самое для большого элемента
	.news-grid--main .news-grid__item--large:nth-child(-n+3) &, .news-grid .news-grid__item--large.news-grid__item--has-img & {
		margin-top: $news-item-lg-img-height;
		// Костыль. Высота картинки для большого блока

		@include media('<mob-lg') {
			margin-top: $news-item-lg-img-height-mob-lg; } }

	// сложный селектор нужен, чтобы перебить предыдущие стили с nth-child
	.news-grid .news-grid-slider .news-grid-slider__slide & {

		@include media('<desk-lg') {
			height: 100%;
			margin-top: 0;
			padding-top: $grid-inside-vertical-space + $news-item-lg-img-height;
			//height: calc(100% - #{$news-item-lg-img-height})

			//.news-grid__text
 }			//ax-height: none

		@include media('<mob-lg') {
			padding-top: $grid-inside-vertical-space + $news-item-lg-img-height-mob-lg;
 } }			//height: calc(100% - #{$news-item-lg-img-height-mob-lg})

	.news-simple & {
		padding: 0;
		margin-right: 10px;

		&::before {
			content: none; } }

	& > * {
		z-index: 2;
		position: relative; }

	&::before {
		@include pseudo();
		@include cover($position: null);
		background: $mark-color-alt;
		transform: translate(0, 100%);
		@include news-transition; }

	.no-touchevents a.news-grid__inner:hover & {
		&::before {
			transform: translate(0, 0);
			transition-duration: $animation-speed; } } }

.news-grid__title {
	font-family: $font-alt;
	@include font(1.8rem, 1.3);
	color: inherit;
	font-weight: 700;
	overflow: hidden;
	height: round-float(1.8rem*1.3*2, 1); // костыль, чтобы высоты блоков были четко по дизу
	margin-bottom: 20px;
	transition-property: color;
	transition-duration: $animation-speed;

	.news-grid__item--large & {
		height: auto;
		max-height: round-float(1.8rem*1.3*3, 1); } } // костыль, для больших новостных элементов

.news-grid__text {
	overflow: hidden;
	color: inherit;
	height: round-float($font-size-small*$line-height-small*3, 1); // костыль, чтобы высоты блоков были четко по дизу
	transition-property: color;
	transition-duration: $animation-speed;

	@include media('<mob-lg') {
		height: auto;
		max-height: round-float($font-size-small*$line-height-small*3, 1); } // костыль, чтобы высоты блоков были четко по дизу

	.news-grid__item--large & {
		height: auto;
		max-height: round-float($font-size-small*$line-height-small*7, 1); }

	//+media('<mob-lg')
	@include media('<desk-lg') {
		margin-bottom: $bottom-space-sm; }

	.news-simple & {
		height: auto;
		margin-bottom: $bottom-space; } }

.news-grid__meta {
	position: absolute;
	bottom: $grid-inside-vertical-space;
	width: calc(100% - #{$grid-inside-horizontal-space*2});
	font-size: 1.2rem;
	line-height: 1.4;
	color: $text-color-light;
	@include news-transition;

	time {
		font-style: normal; }

	.visit-view {
		float: right;
		display: none;

		svg path {
			transition-property: fill;
			transition-duration: $animation-speed; } }

	.no-touchevents a.news-grid__inner:hover & {
		color: $text-color;

		path {
			fill: $text-color;
			transition-duration: $animation-speed; } }

	.news-simple & {
		position: relative;
		bottom: auto;
		width: auto; }

	.visit-view > em {
		transition: none; } }

.news-grid__footer {
	position: relative; }

/*news grid wide */
.news-grid__item--wide {
	height: 180px;

	@include media('<mob-lg') {
		height: auto; }

	.news-grid__inner {
		padding-left: 180px;

		@include media('<mob-lg') {
			padding-left: 0; }

		&::before {
			@include pseudo();
			@include cover(null, 0, 0, 180px, 180px);
			background: url(../../img/no-img.jpg) no-repeat center;
			background-size: cover;

			@include media('<mob-lg') {
				content: none; } } }

	.news__label {
		@include media('<mob-lg') {
			display: none; } }

	.news__place {
		@include media('<mob-lg') {
			@include size(30px);
			font-size: 14px;
			line-height: 30px;
			left: 0; } }

	.news-grid__img {
		position: absolute;
		left: 0;
		top: 0;
		width: 180px;
		height: 180px;

		@include media('<mob-lg') {
			display: none; } }

	.news-grid__content {
		height: 180px;

		@include media('<mob-lg') {
			height: auto; } }

	.news-grid__text {
		overflow: hidden;
		height: round-float($font-size-small*$line-height-small*4, 1); // костыль, чтобы высоты блоков были четко по дизу
		margin-bottom: 20px;

		@include media('<mob-lg') {
			height: auto;
			max-height: round-float($font-size-small*$line-height-small*4, 1); } } } // костыль, чтобы высоты блоков были четко по дизу

/*news grid view end */
/*news grid end*/

/*others news* */
.others-news {

	.news-grid__list .news-grid__item {

		@include media('<mob-lg') {
			display: none;

			&:nth-child(-n+2) {
				display: block; }

			&:nth-child(2) {
				margin-bottom: 0; } }

		&:last-child {
			@include media('<desk-hd') {
				display: none; }

			@include media('<desk-lg') {
				display: block; }

			@include media('<mob-lg') {
				display: none; } } }

	.news-grid ~ .section__footer {
		padding-top: $bottom-space; } }

/*others news end* */

/*news simple */
.news-simple {
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.news-simple__list {
	@include grid-parent();
	@include clear-spacing;

	.news-grid__item {
		@include grid-item(4);

		@include media('<desk-lg') {
			@include grid-item(2); }

		@include media('<mob-md') {
			@include grid-item(1); } }

	.no-touchevents & .news-grid__inner:hover {
		.news-grid__text {
			color: $text-color-alt; } } }

/*news simple end */

/*news footer */
.news__footer {
	text-align: center;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

/*news footer end */

/*dep-banner */
.dep-banner {
	height: auto; }

.dep-banner__inner {
	display: block;
	position: relative;
	text-decoration: none;
	color: white;
	overflow: hidden;
	padding: 30px;
	background: #fff;
	background-repeat: no-repeat;
	background-size: cover;
	@include clear-spacing;

	&::before {
		content: "";
		display: inline-block;
		vertical-align: middle;
		height: 120px;

		@include media('<mob-md') {
			content: none; } }

	.gosdep-banner & {
		background-color: #2d1c74;
		background-image: url(../../img/bg-gosdep.jpg); } }

.dep-banner__img {
	display: inline-block;
	vertical-align: middle;
	width: 120px;
	height: 120px;
	margin-right: 30px;

	@include media('<mob-md') {
		display: block;
		width: auto;
		margin: 0 0 20px; } }

.dep-banner__content {
	display: inline-block;
	vertical-align: middle;
	width: calc(100% - 120px - 40px);
	letter-spacing: normal;

	@include media('<mob-md') {
		display: block;
		width: auto; } }

.dep-banner__title {
	@include font-title;
	@include font(2.0rem, round-float(24/20, 2));
	margin-bottom: 0.6em;

	&:last-child {
		margin-bottom: 0; } }

.dep-banner__text {
	@include font-title;
	@include font(1.4rem, round-float(24/14, 2));
	color: #b2b2ab; }

/*dep-banner end */
